import styled from "styled-components";
import { mobile } from "../responsive";


const Container = styled.div`
    width: 100vw;
    height: 100vh;
    background: linear-gradient(
        rgba(255, 255, 255, 0.5),
        rgba(255, 255, 255, 0.5)
    ),
    url("https://cdn.vox-cdn.com/thumbor/O-OfFzXSv-3oqkoy2GCceTbNUHw=/0x0:2040x1360/1200x800/filters:focal(857x517:1183x843)/cdn.vox-cdn.com/uploads/chorus_image/image/70265887/acastro_210511_1777_psRestock_0004.0.jpg") 
    center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
`;

const Wrapper = styled.div`
        padding: 20px;
        width: 40%;
        background-color: white;
        ${mobile({ width: "75%" })}
`;

const Title = styled.h1`
        font-size: 24px;
        font-weight: 300;

`;

const Form = styled.form`
        display: flex;
        flex-wrap: wrap;
`;

const Input = styled.input`
        flex: 1;
        min-width: 40%;
        margin: 20px 10px 0px 0px;
        padding: 10px;
`;

const Agreement = styled.span`
        font-size: 12px;
        margin: 20px 0px;
`;

const Button = styled.button`
        width: 40%;
        border: none;
        padding: 15px 20px;
        cursor: pointer;
`;
    
const Register = () => {
    return (
        <Container>
            <Wrapper>
                <Title>CREATE AN ACCOUNT</Title>
                <Form>
                    <Input placeholder="name"/>
                    <Input placeholder="last name"/>
                    <Input placeholder="username"/>
                    <Input placeholder="email"/>
                    <Input placeholder="password"/>
                    <Input placeholder="confirm password"/>
                    <Agreement>
                    By creating an account, I consent to the processing of my personal
            data in accordance with the <b>PRIVACY POLICY</b>
                    </Agreement>
                    <Button>Sign Up</Button>
                </Form>
            </Wrapper>
        </Container>
    )
}

export default Register
