import { Link } from "react-router-dom";
import { 
  Facebook, 
  Instagram, 
  Pinterest, 
  Twitter, 
  LocationOn, 
  ContactsOutlined,
} from "@material-ui/icons";
import styled from "styled-components"
import {mobile} from "../responsive"

const Container = styled.div`
  display: flex;
  ${mobile({ flexDirection: "column"})}
  
`;

const Left = styled.div`
flex: 1;
display: flex;
flex-direction: column;
padding: 20px;
`;

const Logo = styled.h1``;

const Desc = styled.p`
margin: 20px 0px;
`;

const SocialContainer = styled.div`
display: flex;
`;

const SocialIcon = styled.div`
width: 40px;
height: 40px;
border-radius: 50%;
color: white;
background-color: #${(props) => props.color};
display: flex;
align-items: center;
justify-content: center;
margin-right: 20px;
`;

const Center = styled.div`
  flex: 1;
  padding: 20px;
  ${mobile({ display: "none" })}

`;

const Title = styled.h3`
  margin-bottom: 30px;
`;

const List = styled.ul`
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
`;

const ListItem = styled.li`
  width: 50%;
  margin-bottom: 10px;
`;

const Right = styled.div`
  flex: 1;
  padding: 20px;
  ${mobile({ backgroundColor: "#eee"})}
`;

const ContactItem = styled.div`
  margin-bottom: 20px;
  display: flex;
  align-items: center;
`;

const Payment = styled.img`
  width: 50%;
`;

const Footer = () => {
  return (
      <Container>
          <Left>
              <Logo>FeltPro Games</Logo>
              <Desc>
              For this ecommerce site, I have used React with stylised components to create a stylish website selling ps5 games and accessories. The pages are linked by React-router-dom. It is a Front-end focused project.
              </Desc>
              <SocialContainer>
              <SocialIcon color="3B5999">
                  <Facebook />
              </SocialIcon>
              <SocialIcon color="E4405F">
                  <Instagram />
              </SocialIcon>
              <SocialIcon color="55ACEE">
                  <Twitter />
              </SocialIcon>
              <SocialIcon color="E60023">
                  <Pinterest />
              </SocialIcon>
              </SocialContainer>
          </Left>
          <Center>
              <Title>FeltPro Games Links</Title>
              <List>
                  <ListItem>Home</ListItem>
                  <ListItem>Cart</ListItem>
                  <ListItem>Games</ListItem>
                  <ListItem>Accessories</ListItem>
                  <ListItem>My Account</ListItem>
                  <ListItem>Order Tracking</ListItem>
                  <ListItem>Discounts</ListItem>
                  <ListItem>Wishlist</ListItem>
                  <ListItem>Terms</ListItem>
                  <ListItem>Contact</ListItem>
              </List>
          </Center>
          <Right>
              <Title>Contact</Title>
              <ContactItem>
                  <ContactsOutlined style={{marginRight: "10px"}}/>www.feltpro.co.uk
              </ContactItem>
              <ContactItem>
                  <LocationOn style={{marginRight: "10px"}}/>Bristol, UK
              </ContactItem>
              <Payment src="https://i.ibb.co/Qfvn4z6/payment.png"></Payment>
          </Right>
      </Container>
  )
}

export default Footer
