import styled from "styled-components";
import {mobile} from "../responsive";

const Container = styled.div`
    width: 100vw;
    height: 100vh;
    background: linear-gradient(
        rgba(255, 255, 255, 0.1),
        rgba(255, 255, 255, 0.5)
    ),
    url("https://www.gameinformer.com/sites/default/files/2021/06/09/2abda14a/ps5_dualsesnse_update.jpg") 
    center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
`;

const Wrapper = styled.div`
        padding: 20px;
        width: 25%;
        background-color: white;
        ${mobile({ width: "75%" })}
`;

const Title = styled.h1`
        font-size: 24px;
        font-weight: 300;

`;

const Form = styled.form`
        display: flex;
        flex-direction: column;
`;

const Input = styled.input`
        flex: 1;
        min-width: 40%;
        margin: 10px 0px;
        padding: 10px;
`;

const Button = styled.button`
        width: 40%;
        border: none;
        padding: 15px 20px;
        cursor: pointer;
        margin-bottom: 10px;
`;

const Link = styled.a`
        margin: 5px 0px;
        font-size: 12px;
        text-decoration: underline;
        cursor: pointer;
`;

const Login = () => {
    return (
        <Container>
            <Wrapper>
                <Title>SIGN IN</Title>
                <Form>
                    <Input placeholder="username"/>
                    <Input placeholder="password name"/>
                    <Button>LOGIN</Button>
                    <Link>FORGOTTEN USERNAME</Link>
                    <Link>CREATE NEW PASSWORD</Link>
                </Form>
            </Wrapper>
        </Container>
    )
}

export default Login
